.main-header {
  background-color: @white;
  padding-top: 13px;

  @media (min-width: @desktop-width) {
    background-color: rgba(0, 0, 0, 0);
  }
}

.main-header__row {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
